define(function() {
    "use strict";
    var E = require("../../core/event-type.js");
    var Util = require("../../core/util.js");
    var Headers = {

        setColumns: function(columns) {
            this.columns = columns;
            this.blankColumn = this.getBlankColumn();
            this.columnsById = {};
            for (var i = 0, l = columns.length; i < l; i++) {
                var m = columns[i];
                this.columnsById[m.id] = i;
            }
            return this;
        },

        getColumns: function() {
            return this.columns;
        },

        //get column index by id
        getColumnIndex: function(id) {
            return this.columnsById[id];
        },

        getColumnColspan: function(column) {
            var colspan = 0;
            var self = this;
            if (Util.islist(column.subs)) {
                column.subs.forEach(function(item) {
                    var l = self.getColumnColspan(item);
                    if (l) {
                        colspan += l;
                    } else {
                        colspan += 1;
                    }
                });
            }
            return colspan;
        },

        getBlankColumn: function() {
            return this.columns[this.columns.length - 1];
        },

        //==========================================================================================

        initHeaders: function() {

            var self = this;

            this.$headerL.empty();
            this.$headerR.empty();

            this.$headerL.css("visibility", "hidden");
            this.$headerR.css("visibility", "hidden");

            this.renderHeaderColumns();

            //async render headers
            clearTimeout(this.timeout_render_headers);
            this.timeout_render_headers = setTimeout(function() {

                self.$headerL.css("visibility", "");
                self.$headerR.css("visibility", "");

                //header completed
                self.initialized = true;
                //self.trigger(E.onInit, this);

                self.headersCompleteHandler();

            }, 20);

            return this;
        },

        headersCompleteHandler: function() {

            //header sort and resize
            this.setSortColumn(this.sortColumn);
            this.setColumnResizeBar();

            this.updateColumnsWidth();

            this.resize();

        },

        //==========================================================================================

        updateColumnsWidth: function() {

            var option = this.option;
            var columns = this.columns;

            //update left right total width
            var columnsWidthL = 0;
            var columnsWidthR = 0;

            var len = columns.length;
            //x position
            var x = 0;
            for (var i = 0; i < len; i++) {
                var colomn = columns[i];
                colomn.tg_x = x;
                var width = colomn.width;
                x += width;
                if (this.hasFrozenColumns && i > option.frozenColumn) {
                    columnsWidthR += width;
                } else {
                    columnsWidthL += width;
                }
            }

            this.columnsWidthL = columnsWidthL;
            this.columnsWidthR = columnsWidthR;

            //total width
            this.columnsWidth = columnsWidthL + columnsWidthR;

        },

        //==========================================================================================
        //only height need update, width is same as container always
        updateHeaderHeight: function() {

            this.headerHeight = 0;

            if (!this.option.showHeader) {
                this.$paneHeader.height(0);
                return;
            }

            //set header table same height
            var TL = this.$headerL.find(".tg-header-table");
            var TR = this.$headerR.find(".tg-header-table");

            //reset height
            TL.css({
                display: "block",
                height: ""
            });
            TR.css({
                display: "block",
                height: ""
            });

            var TLH = TL.height();
            var TRH = TR.height();

            var TH = Math.max(TLH, TRH);

            //fixed height

            TL.css({
                display: "",
                height: TH
            });
            TR.css({
                display: "",
                height: TH
            });

            this.$paneHeader.height(TH);
            this.headerHeight = this.$paneHeader.outerHeight();

            //async update column drag bar height
            this.updateColumnResizeBarHeight();

        },

        updateColumnResizeBarHeight: function() {
            var columnElements = this.$headers.find(".tg-column-item");
            //resizable handle height
            columnElements.each(function() {
                var $elem = $(this);
                //jquery 3 height() does NOT work in IE11
                var h = this.clientHeight;
                $elem.find(".tg-column-resize-bar").height(h);
            });
        },

        //==========================================================================================
        //render headers

        renderHeaderColumns: function() {
            var option = this.option;
            var columns = this.columns;

            var columnsL = [];
            var columnsR = [];

            for (var i = 0, l = columns.length; i < l; i++) {
                var column = columns[i];
                if (this.hasFrozenColumns && i > option.frozenColumn) {
                    columnsR.push(column);
                } else {
                    columnsL.push(column);
                }
            }
            this.columnRootGroups = [];
            //left list
            this.renderHeaderColumnList(columnsL, this.$headerL, 0);
            //right list
            this.renderHeaderColumnList(columnsR, this.$headerR, option.frozenColumn + 1);
            //
            this.initHeaderColumnGroups();
        },

        renderHeaderColumnList: function(columnsList, container, indexStart) {
            var $table = $("<table/>").addClass("tg-header-table").attr({
                cellpadding: 0,
                cellspacing: 0,
                border: 0
            }).appendTo(container);

            //bodyer
            var $tbody = $("<tbody/>").appendTo($table);
            //level
            var totalLevel = this.option.columnsInfo.totalLevel;
            var level = 0;
            while (level <= totalLevel) {
                $("<tr/>").attr("level", level).appendTo($tbody);
                level++;
            }

            var self = this;
            columnsList.forEach(function(column, index) {
                self.renderHeaderColumnItem(column, index + indexStart, $table);
            });
        },

        renderHeaderColumnItem: function(column, i, $table) {
            var $tr = $table.find("tr[level='" + column.tg_header_level + "']");
            var $td = $("<td/>").addClass("tg-column-item").appendTo($tr);

            var $node = $("<div/>").addClass("tg-header-column")
                .addClass(column.headerClass)
                .addClass("tg-align-" + (column.align))
                .attr("title", column.title)
                .attr("data", column.id)
                .attr("index", i)
                .appendTo($td);

            column.tg_node = $node;

            var $name = $("<div/>").addClass("tg-column-name").appendTo($node);
            $name.addClass(column.headerNameClass);

            var value = column.name;

            var content = value;
            //formatter
            if (typeof(column.tg_headerFormatter) === "function") {
                //value, rowData, columnData, rowIndex, columnIndex, cellNode
                content = column.tg_headerFormatter(value, this.columns, column, -1, i, $name);
            }
            $name.html(content);

            //width handler
            var columnWidth = $node.parent().width();
            if (Util.isnum(column.width)) {
                columnWidth = column.width;
            }
            columnWidth = Util.clamp(columnWidth, column.minWidth, column.maxWidth);

            this.setColumnNodeWidth($node, columnWidth);

            column.width = columnWidth;

            //blank column handler
            if (column.id === this.blankColumn.id) {
                //console.log(columnWidth);
                return;
            }

            //rowspan handler
            var rowspan = column.tg_rowspan;
            if (rowspan && rowspan > 1) {
                $td.attr("rowspan", rowspan);
            }

            //sort elem handler
            this.renderHeaderColumnSort(column, $td);

            //column rendered
            this.trigger(E.onHeaderCellRendered, {
                column: column,
                type: "leaf",
                node: $node
            });

            //parent group handler
            this.renderHeaderColumnGroup(column, $table);
        },

        //==========================================================================================

        renderHeaderColumnGroup: function(column, $table) {

            if (!column) {
                return;
            }

            var group = column.tg_parent;
            //root
            if (!group) {
                //console.log(column.name, column.tg_deep);
                return;
            }

            this.renderGroup(group, $table);
        },

        renderGroup: function(group, $table) {

            if (group.tg_rendered) {
                return;
            }

            // create group container
            var $trParent = $table.find("tr[level='" + group.tg_level + "']");
            var $tdParent = $("<td/>").addClass("tg-column-item tg-column-group-item").appendTo($trParent);

            //rowspan handler
            var rowspan = group.tg_rowspan;
            if (rowspan && rowspan > 1) {
                $tdParent.attr("rowspan", rowspan);
            }

            //colspan handler
            var colspan = this.getColumnColspan(group);
            if (colspan > 1) {
                $tdParent.attr("colspan", colspan);
            }

            var $nodeGroup = $("<div/>").addClass("tg-header-group")
                .addClass(group.headerClass)
                .addClass("tg-align-" + (group.align))
                .attr("title", group.title)
                .attr("data", group.id)
                .data("data", group)
                .appendTo($tdParent);

            group.tg_node = $nodeGroup;

            var $groupName = $("<div/>").addClass("tg-column-group-name").appendTo($nodeGroup);
            $groupName.addClass(group.headerNameClass).html(group.name);

            //group rendered
            this.trigger(E.onHeaderCellRendered, {
                column: group,
                type: "group",
                node: $tdParent
            });

            group.tg_rendered = true;

            //store column root group
            if (!group.tg_parent) {
                this.columnRootGroups.push(group);
            }

            this.renderHeaderColumnGroup(group, $table);
        },

        //==========================================================================================

        initHeaderColumnGroups: function() {
            //console.log(this.columnRootGroups);
            for (var i = 0, l = this.columnRootGroups.length; i < l; i++) {
                var group = this.columnRootGroups[i];
                this.updateGroupWidth(group);
            }
        },

        updateGroupWidth: function(group) {
            if (!group || !Util.islist(group.subs)) {
                return;
            }
            var width = 0;
            var self = this;
            group.subs.forEach(function(item) {
                if (item.tg_group) {
                    self.updateGroupWidth(item);
                }
                if (Util.isnum(item.width)) {
                    width += item.width;
                }
            });
            this.setColumnNodeWidth(group.tg_node, width);
            group.width = width;
        },

        updateColumnGroupWidth: function(column) {
            if (!column || !column.tg_parent) {
                return;
            }
            var group = column;
            while (group.tg_parent) {
                group = group.tg_parent;
            }
            this.updateGroupWidth(group);
        },

        //==========================================================================================
        //render sort
        renderHeaderColumnSort: function(column, $td) {

            var header = $td.find(".tg-header-column");

            if (column.sortable) {
                header.addClass("tg-header-sortable");
            }

            var $sortElem = $("<div/>").addClass("tg-sortable-placeholder").appendTo(header);

            var $indicator = $("<div/>").addClass("tg-sort-indicator").appendTo($sortElem);
            $("<div/>").addClass("tg-sort-icon tg-sort-asc").appendTo($indicator);
            $("<div/>").addClass("tg-sort-icon tg-sort-desc").appendTo($indicator);

            //set default sort status
            var o = this.option;
            if (column.id === o.sortField) {
                if (o.sortAsc) {
                    header.addClass("tg-sort-indicator-asc");
                    column.sortAsc = true;
                } else {
                    header.addClass("tg-sort-indicator-desc");
                    column.sortAsc = false;
                }
                header.addClass("tg-header-column-sorted");
            }

        },

        setSortColumn: function(sortColumn) {

            if (!sortColumn) {
                return;
            }

            this.sortColumn = sortColumn;

            //remove all first
            var sortColumnElems = this.$headers.find(".tg-header-column");
            sortColumnElems.removeClass("tg-sort-indicator-asc");
            sortColumnElems.removeClass("tg-sort-indicator-desc");
            sortColumnElems.removeClass("tg-header-column-sorted");

            //add indicator
            var indicator = sortColumn.sortAsc ? "tg-sort-indicator-asc" : "tg-sort-indicator-desc";
            var sortColumnElem = sortColumnElems.filter("[index='" + sortColumn.tg_index + "']");
            sortColumnElem.addClass(indicator).addClass("tg-header-column-sorted");
        },

        //==========================================================================================

        setColumnResizeBar: function() {

            var columns = this.columns;

            var columnElements = this.$headers.find(".tg-column-item");
            columnElements.find(".tg-column-resize-bar").remove();

            var hasResizable = false;
            columnElements.each(function() {
                var $elem = $(this);
                var $node = $elem.find(".tg-header-column");
                if (!$node.length) {
                    return;
                }
                var index = parseInt($node.attr("index"), 10);
                var column = columns[index];
                if (column && column.resizable) {
                    hasResizable = true;
                    $("<div/>").addClass("tg-column-resize-bar").appendTo($elem);
                }
            });

            var $resizeBar = this.$headers.find(".tg-column-resize-bar").unbind();

            if (!hasResizable) {
                return;
            }

            var self = this;
            $resizeBar.unbind().bind("mousedown", function(e) {
                self.columnWidthDrag.start(e, $(this));
            });

        },

        //==========================================================================================

        setColumnNodeWidth: function($node, width) {
            if (!$node) {
                return;
            }
            //do NOT use $node.width(width), it has column border issue
            $node.css({
                width: width
            });
            $node.parent().css({
                width: width
            });

            //if width is 0, height fix
            if (width <= 0) {
                $node.hide();
            } else {
                $node.show();
            }

        },

        setColumnWidth: function(columnItem, width) {

            //change width for column data
            var newWidth = Util.clamp(width, columnItem.minWidth, columnItem.maxWidth);

            //change width for column elements, both for all parent container
            var $node = $(columnItem.tg_node);
            this.setColumnNodeWidth($node, newWidth);

            columnItem.width = newWidth;

            this.updateColumnGroupWidth(columnItem);

            //update for all
            this.updateColumnsWidth();
            this.updateCanvasWidth(true);

            this.trigger(E.onColumnWidthChanged, columnItem);

        }

    };

    return Headers;

});


