define(function() {
    "use strict";

    var E = require("../../core/event-type.js");
    var Util = require("../../core/util.js");

    var ModelRows = {

        //=============================================================================
        //add/delete rows

        addRow: function(rowItem, scrollTo) {
            this.addRows([rowItem], scrollTo);
            return this;
        },

        addRows: function(rowList, scrollTo) {
            if (!Util.islist(rowList)) {
                return this;
            }
            var self = this;
            var lastRow = null;
            var hasAdded = false;
            rowList.forEach(function(row) {
                if (!row) {
                    return;
                }
                self.rows.push(row);
                lastRow = row;
                hasAdded = true;
            });
            if (!hasAdded) {
                return this;
            }
            if (scrollTo) {

                this.one(E.onRenderUpdate, function(e, d) {
                    this.scrollToRow(lastRow);
                });

            }
            this.render();
            return this;
        },

        deleteRow: function(rowItem) {
            this.deleteRows([rowItem]);
            return this;
        },

        deleteRows: function(rowList) {
            if (!Util.islist(rowList)) {
                return this;
            }
            var self = this;
            rowList.forEach(function(row) {
                var item = self.getRowItem(row);
                if (!item) {
                    return;
                }
                var list = self.rows;
                if (item.tg_parent) {
                    list = item.tg_parent.subs;
                }
                for (var i = 0; i < list.length; i++) {
                    if (item === list[i]) {
                        self.invalidateRowsFrom(item.tg_index);
                        list.splice(i, 1);
                        break;
                    }
                }
            });
            this.render();
            return this;
        },

        //=============================================================================

        //row subs
        setRowSubs: function(rowIndex, subs) {
            var item = this.getRowItem(rowIndex);
            if (!item) {
                return this;
            }
            if (Util.islist(subs)) {
                //open subs default
                item.collapsed = false;
            }
            item.subs = subs;
            this.initRowsData();
            this.gridView.invalidateRowsFrom(item.tg_index);
            return this;
        },

        getRowParentSubs: function(rowIndex) {
            var item = this.getRowItem(rowIndex);
            if (!item) {
                return null;
            }
            //root.subs
            var subs = this.rows;
            if (item.tg_parent) {
                subs = item.tg_parent.subs;
            }
            return subs;
        },

        //=============================================================================
        //frozen row
        getFrozenRowByRowTypeList: function(rows, frozenRowTypeList) {
            var frozenRow = -1;
            if (!Util.islist(rows) || !Util.islist(frozenRowTypeList)) {
                return frozenRow;
            }
            for (var i = 0, l = rows.length; i < l; i++) {
                var row = rows[i];
                if (Util.inlist(row.rowType, frozenRowTypeList)) {
                    frozenRow += 1;
                    continue;
                }
                break;
            }
            return frozenRow;
        },

        //TODO should be sort by back-end
        sortRowsByRowTypeList: function(rows, rowTypeList) {
            if (!Util.islist(rows) || !Util.islist(rowTypeList)) {
                return;
            }
            var tempList = [];
            rowTypeList.forEach(function(rowType) {
                for (var i = 0, l = rows.length; i < l; i++) {
                    var row = rows[i];
                    if (row.rowType === rowType) {
                        tempList.unshift(row);
                        rows.splice(i, 1);
                        break;
                    }
                }
            });
            if (!tempList.length) {
                return;
            }
            tempList.forEach(function(item) {
                rows.unshift(item);
            });
        },

        //=============================================================================
        //highlight single row

        setHighlightRow: function(rowIndex) {
            var item = this.getRowItem(rowIndex);
            if (!item) {
                return;
            }
            if (item.highlight) {
                return;
            }
            //remove previous highlight row
            var self = this;
            this.forEachRow(function(row, i, parent) {
                if (row.highlight) {
                    self.removeRowStatus(row, "highlight");
                }
            });
            this.addRowStatus(item, "highlight");
        },

        //=============================================================================
        //change row status, the status will be add/remove as className
        //current support: selected, highlight

        addRowStatus: function(rowItem, status) {
            if (!rowItem) {
                return;
            }
            rowItem[status] = true;
            this.updateRowStatus(rowItem, status);
        },

        removeRowStatus: function(rowItem, status) {
            if (!rowItem) {
                return;
            }
            rowItem[status] = false;
            this.updateRowStatus(rowItem, status);
        },

        updateRowStatus: function(rowItem, status) {
            if (!this.gridView.initialized) {
                return;
            }
            // update row class
            var rowNodes = this.gridView.getRowNodes(rowItem.tg_index);
            if (rowNodes) {
                var hasStatus = rowItem[status];
                if (hasStatus) {
                    rowNodes.addClass(status);
                } else {
                    rowNodes.removeClass(status);
                }
            }
        }

    };

    return ModelRows;

});


