define(function() {
    "use strict";

    require("./css/edit-grid.css");

    var Util = require("../core/util.js");
    var TreeGrid = require("../tree-grid/tree-grid.js");

    var EditFormatter = require("./edit-formatter.js");

    var EditGrid = TreeGrid.extend({

        defaultOption: function() {
            return Util.merge(TreeGrid.prototype.defaultOption(), {
                className: "ms-tree-grid-editor",
                editable: true
            });
        },

        defaultEditor: function() {
            return Util.merge(TreeGrid.prototype.defaultEditor(), {
                text: require("./editor/text.js"),
                list: require("./editor/list.js")
            });
        },

        defaultFormatter: function() {
            return Util.merge(TreeGrid.prototype.defaultFormatter(), EditFormatter);
        },

        //=====================================================================================

        render: function(invalidateAll) {
            this.renderNow(invalidateAll);
            this.getRowsItemIdMap();
        },

        getRowsItemIdMap: function() {
            var self = this;
            this.rowsItemIdMap = {};
            $.each(this.gridRowsData, function(i, row) {
                var id = row.id;
                if (id) {
                    id = id.toLowerCase();
                    self.rowsItemIdMap[id] = row;
                }
            });
            return this.rowsItemIdMap;
        },

        addRows: function(rows, callback) {
            var self = this;
            $.each(rows, function(i, row) {
                self.rows.push(row);
                if (callback) {
                    callback(row);
                }
            });
            this.render();
        },

        deleteRow: function(id) {
            var rowsData = this.rows;
            this.removeRowFromRowsDataSource(rowsData, id);
            this.gridView.invalidateAll();
            this.renderNow();
        },
        removeRowFromRowsDataSource: function(rowsData, id) {
            var self = this;
            $.each(rowsData, function(index, itemRowData) {
                if (itemRowData && itemRowData.id === id) {
                    rowsData.splice(index, 1);
                    return false; /*break each*/
                }
                var subs = itemRowData.subs;
                if (Util.islist(subs)) {
                    self.removeRowFromRowsDataSource(subs, id);
                }
            });
        },

        getColumnIndex: function(columnId) {
            if (this.gridView) {
                return this.gridView.getColumnIndex(columnId);
            }
            return null;
        },

        getRowItemById: function(id) {
            id = id.toLowerCase();
            return this.rowsItemIdMap[id];
        },

        toString: function() {
            return "[object EditGrid]";
        }
    });
    return EditGrid;
});


