define(function() {
    "use strict";

    var EventBase = require("../../core/event-base.js");
    var E = require("../../core/event-type.js");

    var GridViewBase = EventBase.extend({

        // Public API

        find: function(context, container) {
            return $(container || this.$container).find(context);
        },

        getNodeIndex: function(node) {
            var index = parseInt($(node).attr("index"), 10);
            return index;
        },

        disableSelection: function(target) {
            $(target).attr("unselectable", "on").css("MozUserSelect", "none").bind("selectstart.ui", function() {
                return false;
            });
        },

        initDocumentBody: function() {
            /* jshint ignore:start */
            if (!document.body) {
                document.open();
                document.write("<body></body>");
                document.close();
            }
            /* jshint ignore:end */
        },

        initScrollbarSize: function() {
            this.initDocumentBody();
            var div = document.createElement("div");
            div.style.overflow = "scroll";
            div.style.width = "100px";
            div.style.height = "100px";
            document.body.appendChild(div);
            var w = div.offsetWidth - div.clientWidth;
            var h = div.offsetHeight - div.clientHeight;
            document.body.removeChild(div);
            this.scrollbarWidth = w > 0 ? w : 17;
            this.scrollbarHeight = h > 0 ? h : 17;
            return this;
        },

        initMaxSupportedCssHeight: function() {
            this.initDocumentBody();
            var div = document.createElement("div");
            div.style.position = "absolute";
            document.body.appendChild(div);

            var maxSupportedHeight = 1000000;

            var j = 0;

            var firefoxCheck = function(min, max) {

                //console.log(j);
                if (j > 30) {
                    return;
                }
                j++;

                var mid = min + Math.round((max - min) * 0.5);
                div.style.height = mid + "px";
                var realHeight = div.clientHeight;

                //console.log(min, mid, max, realHeight);

                if (realHeight) {
                    if (max - mid < 2) {
                        maxSupportedHeight = realHeight;
                    } else {
                        firefoxCheck(mid, max);
                    }

                } else {
                    firefoxCheck(min, mid);
                }
            };

            var i = 0;
            while (i < 10) {
                var test = maxSupportedHeight * 2;
                div.style.height = test + "px";
                var realHeight = div.clientHeight;
                if (!realHeight) {
                    //for firefox check
                    firefoxCheck(maxSupportedHeight, test);
                    break;
                }
                if (realHeight < test) {
                    maxSupportedHeight = realHeight;
                    break;
                }
                maxSupportedHeight = test;
                i++;
            }

            document.body.removeChild(div);

            //floor 1000
            maxSupportedHeight = Math.floor(maxSupportedHeight * 0.001) * 1000;

            //min support is ie11, max size is 153w+, line height is 27px, so max lines is 5w+
            var iemh = 1530000;
            maxSupportedHeight = Math.max(iemh, maxSupportedHeight);

            this.maxSupportedHeight = maxSupportedHeight;

            //console.log(this.maxSupportedHeight);

            return this;
        },

        getTextWidth: function(text) {

            var $text = $("<span/>").css("visibility", "hidden").html(text).appendTo(this.$container);
            var width = $text.width();
            $text.remove();

            return width;
        },


        showLoading: function() {
            if (this.$container) {
                this.$container.find(".tg-loading").show();
            }
            return this;
        },

        hideLoading: function() {
            if (this.$container) {
                this.$container.find(".tg-loading").hide();
            }
            return this;
        },

        destroy: function() {
            this.trigger(E.onDestroy);
            this.editController.cancelCurrentEdit();
            this.unbindAncestorScrollEvents();
            this.$container.removeAttr("tg_id");
            this.$container.unbind(".tg");
            this.removeCssRules();
            this.unbind();
            this.$container.empty().removeClass(this.uid);
        }

    });

    return GridViewBase;

});


