define(function() {
    "use strict";
    var ListEditor = function(args) {
        var $select;
        var $html;
        var defaultValue = "";
        var item = args.rowItem;
        var id = args.columnItem.id;
        var container = args.container;
        this.init = function() {
            var listOptions = args.columnItem.editorData || [{
                id: "true",
                name: "True"
            }, {
                id: "false",
                name: "False"
            }];
            var itemValue = item[id];
            var html = "<div class='input-group'><select tabIndex='0' class='editor-list'><option value=''></option>";
            $.each(listOptions, function(i, option) {
                var id = option.id;
                var name = option.name;
                if (itemValue === id) {
                    html += "<option value='" + id + "' selected='selected'>" + name + "</option>";
                } else {
                    html += "<option value='" + id + "'>" + name + "</option>";
                }
            });
            html += "</select></div>";
            $html = $(html);
            $html.appendTo(container);
            $html.unbind("mousedown.booleselectel").bind("mousedown.booleselectel", function(e) {
                $(".select2-dropdown").addClass("tg-list-select2-dropdown");
            });
            $select = $html.find('.editor-list');
            $select.select2({
                width: "100%"
            });
            $select.on("select2:close", function() {
                args.doCommitCurrentEdit.call(args.grid);
                $select.focus();
            });
            this.scrollToCurrentRow();
            $select.focus();
            var evt = $.Event("mousedown");
            evt.which = 1;
            $html.find(".select2-selection__rendered").trigger(evt);
        };

        this.scrollToCurrentRow = function() {
            var parentContainer = container.parent();
            var parentPos = parentContainer.position();
            var parentTop = parentPos.top;
            var panelY = args.grid.$scrollPaneY;
            var absTop = parentTop - panelY[0].scrollTop;
            if ((absTop + 27) > panelY.height()) {
                panelY[0].scrollTop += 27;
                return;
            }
            var $canvasBottomR = args.grid.$canvasBottomR;
            var height = $canvasBottomR.height();
            if ((parentTop + 27) === height) {
                $html.find(".select2-selection").css("height", "26px");
            }
        };

        this.destroy = function() {
            $(".select2-dropdown").removeClass("tg-list-select2-dropdown");
            $select.remove();
        };

        this.focus = function() {
            $select.focus();
        };

        this.loadValue = function(item) {
            var v = item[id];
            if (v !== undefined && v !== null) {
                defaultValue = v;
                $select.val(v);
                $select.select();
            }
        };

        this.serializeValue = function() {
            return $select.val();
        };

        this.applyValue = function(item, state) {
            item[id] = state;
        };

        this.isValueChanged = function() {
            return ($select.val() !== defaultValue);
        };

        this.validate = function() {
            var val = $select.val();
            if (val === "" || val === null) {
                return {
                    valid: false,
                    msg: "Please choose one"
                };
            }
            return {
                valid: true,
                msg: null
            };
        };
        this.toString = function() {
            return "list";
        };

        this.init();
    };
    return ListEditor;
});


