define(function() {
    "use strict";

    var E = require("../../core/event-type.js");
    var Util = require("../../core/util.js");

    var ModelRowDragdrop = {

        //row drag handler
        rowDragStartHandler: function(e, d) {

            //console.log(e.type, d);
            var item = d.row;
            //console.log(row);
            if (!item) {
                return;
            }

            d.rowTop = this.gridView.getRowTop(item.tg_index);

            var rowNodes = this.gridView.getRowNodes(item.tg_index);
            if (!rowNodes) {
                return;
            }

            d.rowNodes = rowNodes;

            var rowCloneNodes = this.getRowCloneNodes(rowNodes);
            d.rowCloneNodes = rowCloneNodes;

            var rowPlaceholder = this.getRowPlaceholder(rowNodes);
            d.rowPlaceholder = rowPlaceholder;

            this.rowDragPositionHandler(d);

            this.collapseParentSubs(item);
            this.addRowStatus(item, "dragging");

            this.rowDragRangeListHandler(d);

        },

        rowDragUpdateHandler: function(e, d) {
            //console.log(e.type, d);
            this.rowDragPositionHandler(d);

            this.rowDragPlaceholderHandler(d);

            this.rowDragAutoScrollHandler(d);

        },

        rowDragCompleteHandler: function(e, d) {
            //console.log(e.type, d);
            this.rowDragCleanHandler(d);

            //sort
            this.rowDragSortHandler(d);

            //drag drop
            this.trigger(E.onRowDropped, d);

        },

        rowDragCleanHandler: function(d) {

            if (d.row) {
                this.removeRowStatus(d.row, "dragging");
            }

            if (d.rowCloneNodes) {
                d.rowCloneNodes.remove();
                d.rowCloneNodes = null;
            }

            if (d.rowPlaceholder) {
                d.rowPlaceholder.remove();
                d.rowPlaceholder = null;
            }
        },

        rowDragPositionHandler: function(d) {
            var top = d.rowTop + d.mouseOffsetY;
            d.currentRowTop = top;
            //move
            d.rowCloneNodes.each(function(index, elem) {
                var node = $(elem);
                node.css("top", top);
            });
        },

        rowDragRangeListHandler: function(d) {
            var item = d.row;
            var subs = this.getRowParentSubs(item);
            if (!Util.islist(subs)) {
                d.rangeList = [];
                return;
            }
            var rangeList = [];
            var self = this;
            subs.forEach(function(row) {
                var top = self.gridView.getRowTop(row.tg_index);
                rangeList.push({
                    row: row,
                    top: top
                });
            });
            d.rangeList = rangeList;
        },

        getRowCloneNodes: function(rowNodes) {
            var rowCloneNodes = $();
            rowNodes.each(function(index, elem) {
                var node = $(elem);
                var nodeCopy = node.clone();
                nodeCopy.appendTo(node.parent());
                rowCloneNodes = rowCloneNodes.add(nodeCopy);
            });
            rowCloneNodes.removeClass("tg-pane-first").addClass("tg-clone");
            return rowCloneNodes;
        },

        getRowPlaceholder: function(rowNodes) {
            var rowPlaceholder = $();
            rowNodes.each(function(index, elem) {
                var node = $(elem);
                var holder = $("<div/>").addClass("tg-row-placeholder").hide();
                holder.appendTo(node.parent());
                rowPlaceholder = rowPlaceholder.add(holder);
            });
            return rowPlaceholder;
        },

        getRowPositionInfo: function(d) {
            var rangeList = d.rangeList;
            if (!Util.islist(rangeList)) {
                return null;
            }
            var info = {};
            /* jshint ignore:start */
            var top = d.currentRowTop;
            var rowHeight = this.option.rowHeight;
            var item = null;
            var length = rangeList.length;
            for (var i = 0; i < length; i++) {
                item = rangeList[i];
                if (!item.row.draggable) {
                    continue;
                }
                if (top <= item.top + rowHeight * 0.5) {
                    info = {
                        index: i,
                        row: item.row,
                        bottom: false
                    };
                    break;
                }
                if (top <= item.top + rowHeight) {
                    info = {
                        index: i,
                        row: item.row,
                        bottom: true
                    };
                    break;
                }
            }

            if (!info.row) {
                info = {
                    index: length - 1,
                    row: item.row,
                    bottom: true,
                    last: true
                };
            }
            /* jshint ignore:end */

            if (info.index === length - 1) {
                info.last = true;
            }

            return info;
        },

        rowDragPlaceholderHandler: function(d) {
            var info = this.getRowPositionInfo(d);
            if (!info) {
                return;
            }

            var row = info.row;

            var top = this.gridView.getRowTop(row.tg_index);
            if (info.bottom) {
                top += this.option.rowHeight;
            }

            if (info.last) {
                top -= 2;
            }

            d.rowPlaceholder.css("top", top).show();

            d.rowPositionInfo = info;

        },

        rowDragAutoScrollHandler: function(d) {
            //var info = d.rowPositionInfo;

            //var row = info.row;
            //var rangeList = d.rangeList;


        },

        rowDragSortHandler: function(d) {
            var info = d.rowPositionInfo;
            if (!info) {
                return;
            }

            //no change
            if (d.row === info.row) {
                return;
            }

            //console.log(info.row.tg_index, info);

            var subs = this.getRowParentSubs(d.row);
            if (!Util.islist(subs)) {
                return;
            }

            //remove from list first
            var removeListItem = function(list, item) {
                for (var i = 0, l = list.length; i < l; i++) {
                    if (item === list[i]) {
                        subs.splice(i, 1);
                        break;
                    }
                }
            };
            removeListItem(subs, d.row);

            //splice to list
            var insertListItem = function(list, item, info) {
                for (var i = 0, l = list.length; i < l; i++) {
                    if (info.row === list[i]) {
                        if (info.bottom) {
                            list.splice(i + 1, 0, item);
                        } else {
                            list.splice(i, 0, item);
                        }
                        break;
                    }
                }
            };
            insertListItem(subs, d.row, info);

            //fresh 
            var invalidateIndex = info.row.tg_index;
            if (d.row.tg_index < info.row.tg_index) {
                invalidateIndex = d.row.tg_index;
            }

            this.removeSortStatus();

            this.initRowsData();
            //this.invalidateAll();
            this.invalidateRowsFrom(invalidateIndex);
            this.renderNow();

        }

    };

    return ModelRowDragdrop;

});


