define(function() {
    "use strict";

    var ModelScroll = {

        //=============================================================================
        //scroll to row/column/cell

        scrollToRow: function(row) {
            var rowItem = this.getRowItem(row);
            this.gridView.scrollToItem(rowItem, null, true);
            return this;
        },

        scrollToColumn: function(column) {
            var columnItem = this.getColumnItem(column);
            this.gridView.scrollToItem(null, columnItem, true);
            return this;
        },

        scrollToCell: function(row, column) {
            var rowItem = this.getRowItem(row);
            var columnItem = this.getColumnItem(column);
            this.gridView.scrollToItem(rowItem, columnItem, true);
            return this;
        },

        //=============================================================================
        //scroll row/column/cell into view

        scrollRowIntoView: function(row) {
            var rowItem = this.getRowItem(row);
            this.gridView.scrollItemIntoView(rowItem, null, true);
            return this;
        },

        scrollColumnIntoView: function(column) {
            var columnItem = this.getColumnItem(column);
            this.gridView.scrollItemIntoView(null, columnItem, true);
            return this;
        },

        scrollCellIntoView: function(row, column) {
            var rowItem = this.getRowItem(row);
            var columnItem = this.getColumnItem(column);
            this.gridView.scrollItemIntoView(rowItem, columnItem, true);
            return this;
        }

    };

    return ModelScroll;

});


